import {
	deleteCollection
} from '@/api/goods.js'
import toFixed from '@/pages/common/to-fixed.vue'
export default {
	props: {
		product: [Array, Object],
		table: {
			type: String,
			default: 'yhq_goods'
		}
	},
	components: {
		toFixed
	},
	data() {
		return {
			renderImage: true
		};
	},
	computed: {},
	onLoad() {

	},
	methods: {
		godetail(id,presale) {
			console.log(id);
			console.log(JSON.stringify(this.productList));
			uni.navigateTo({
				url: `/pages/common/goods-detail?id=${id}`
			})
		},
		cancle(index) {
			console.log('需要取消的ID', this.productList[index].num_iid)
			deleteCollection({
				phone: this.getUser(),
				num_iid: this.productList[index].num_iid
			}).then(res => {
				uni.showToast({
					title: '已取消',
				})
				this.productList.splice(index, 1);
			})
		},
		getUser() {
			try {
				let user = uni.getStorageSync('user');
				return user.phone
			} catch (e) {
				console.log(e);
			}
		}
	},

};
